@import "../../vars.css";

.container {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  background: linear-gradient(180deg, hsla(177, 33%, 79%, 1) 0%, hsla(202, 17%, 42%, 1) 100%);
  box-shadow: 0 0 30px 0 #586F7C;
  padding: 18px 24px;
  max-width: 345px;
}

.image {
  margin-bottom: 26px;
}

.title {
  letter-spacing: 1.75px;
  font-size: 25px;
  font-weight: 700;
}

.description {
  margin-top: 6px;
  font-size: 25px;
  font-weight: 400;
}

.skills {
  width: 100%;
  letter-spacing: 1.5px;
  margin-top: 14px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 6px;
  list-style: none;
}

.skill {
  font-size: 25px;
  font-weight: 400;
  border-radius: 100px;
  background: var(--color-dark);
  color:var(--color-white);
  padding: 2px 22px;
}

.links {
  width: 100%;
  margin-top: 26px;
}

.link {
  text-decoration: none;
  color: var(--color-white);
  font-size: 22px;
  font-weight: 600;
  border-radius: 100px;
  background: var(--color-black);
  padding: 6px 25px;
}